import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state:{
		topics:'',
		page:1
	},
	mutations:{
		SAVE_TOPICS(state,data){
			state.topics=data;
			state.page=1;
			state.page++;
		},
		MORE_TOPICS(state,data){
			state.page++;
			state.topics=state.topics.concat(data);
		}
	}
})


