<template>
	<div id="userpage">
		<div class="introduction clear">
			<img :src="user.avatar_url">
			<span class="username">{{user.loginname}}</span>
			<span class="create_at">{{user.create_at|last_reply_at}}加入cnodejs</span>
			<span class="score">个人积分：{{user.score}}</span>
		</div>
		<div class="recent">
			<div class="tab clear">
				<div class="replies_tab" 
				     @click='changeTab("recent_replies")'
				     :class="{'active':this.active==this.user.recent_replies}">
				     最近回复
				</div>
				<div class="topics_tab" 
				     @click='changeTab("recent_topics")'
				      :class="{'active':this.active==this.user.recent_topics}">
				     最近发表
				</div>
			</div>
			<div class="content clear">
				<div v-for='item in active' 
					 class="item clear">
					<img :src="item.author.avatar_url"  
						 v-link="{name:'user',params:{username:item.author.loginname}}"
						 @click='changeUser(item.author.loginname)'>
					<div class="title"
						 v-link="{name:'topic',params:{id:item.id}}">
						 {{item.title}}
					</div>
					<div class="last_reply_at">
						最近回复：{{item.last_reply_at|last_reply_at}}
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				username:this.$route.params.username,
				user:'',
				active:''
			}
		},
		ready:function(){
			let username=this.username;
			let successFn=(response)=>{
				this.$set('user',response.data.data)
				//初始化active
				this.$set('active',response.data.data.recent_replies)
			};
			let errorFn=(response)=>{
				console.warn(response);
			};
			this.get(username,successFn,errorFn)
			
		},
		methods:{
			get:function(username,successFn,errorFn){
				this.$http({
			      method:'GET',
			      url:'https://cnodejs.org/api/v1/user/'+username
			    }).then((response)=>{
			      successFn(response);
			    }).catch((err)=>{
			      errorFn(err);
			    })
			},
			changeTab:function(name){
				this.$set('active',this.user[name]);
			},
			changeUser:function(username){
				this.user='';
				let successFn=(response)=>{
					this.$set('user',response.data.data)
					//初始化active
					this.$set('active',response.data.data.recent_replies)
				};
				let errorFn=(response)=>{
					console.warn(response);
				};
				this.get(username,successFn,errorFn)
			}

		}

	}
</script>

<style scoped lang='scss'>
	.introduction{
		background-color: #fff;
		margin: 5px;
		padding: 10px;
		position: relative;
		img{
			width: 1.5rem;
			height: 1.5rem;
			border-radius: 15%;
			float: left;
		}
		span{
			position: absolute;
			display: block;
			left:1.7rem;
		}
		.username{
			font-size: .3rem;
			padding: 5px;
		}
		.create_at{
			padding: 5px;
			top:.7rem;
			color: #666666;
		}
		.score{
			padding: 5px;
			top:1.1rem;
			color: #666666;
		}
	}
	.recent{
		background-color: #fff;
		margin: 5px;
		position: relative;
		.tab{
			position: relative;
			width: 100%;
			.active{
				color: #80bd01;
				&:after{
					background-color:#80bd01;
					color: #2e2e2e;
					height:3px;
					margin-top: -3px;
					width: 100%;
					display: block;
					content: "";
				}
			}
			.replies_tab{
				border-right: 1px solid #e2e2e2;
			}
		}
		.tab div{
			display: inline-block;
			width:50%;
			float: left;
			box-sizing: border-box;
			height:.8rem;
			line-height: .8rem;
			font-size: .27rem;
			border-bottom:1px solid #e2e2e2;
		}
		.content{
			.item{
				font-size: .28rem;
				font-weight:600;
				text-align: left;
				border-bottom:1px solid #e2e2e2;
				padding: .1rem 0;
				width: 100%;
				float: left;
				cursor:pointer; 
				img{
					height:.7rem;
					display: block;
					padding: .1rem;
					border-radius: 15%;
					float: left;
					cursor:pointer; 
				}
				.title{
					text-overflow:ellipsis;
					white-space: nowrap;
					overflow: hidden;
					padding-top: .1rem;
				}
				.last_reply_at{
					float: right;
					margin-right: .3rem;
					color: #999999;
					font-size: 12px;
				}
			}
		}
		
	}
</style>


