<template>
	<div id="content">
		<div class="panel ">
			<div class="top clear">
				<div class="title">
					<p>{{title}}</p>
				</div>
				<div class="create_at">发表于 {{create_at|last_reply_at}}</div>
				<div class="tab">{{data|tabName}}</div>
			</div>
			<div class="content clear">
				<div class="author clear"
					 v-link="{name:'user',params:{username:author.loginname}}">
					<img :src="author.avatar_url">
					<span>{{author.loginname}}</span>
				</div>
				
				<div class="main-content"
					 v-html='content'>
				</div>
			</div>
			<div class="replies_count">
				<div class="count">共{{reply_count}}条回复</div>
			</div>
			<div class="replies">
				<div class="reply"
					 v-for='reply in replies'>
					<div class="author clear"
						 v-link="{name:'user',params:{username:reply.author.loginname}}">
						<img :src="reply.author.avatar_url">
						<span class="name">{{reply.author.loginname}}</span>
						<span class="create_at">{{$index}}楼&nbsp&nbsp&nbsp{{reply.create_at|last_reply_at}}</span>
					</div>
					<div class="content"
						 v-html='reply.content'>
						
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	require('../css/content.scss')

	export default{
		data(){
			return{
				id:this.$route.params.id,
				author:'',
				data:''
			}
		},
		ready:function(){
			document.documentElement.scrollTop = document.body.scrollTop =0;
			
			this.$http({
				url:'https://cnodejs.org/api/v1/topic/'+this.$route.params.id,
				method:'GET'
			}).then((response)=>{
				let data=response.body.data
				Vue.set(this,'data',data)
				//将data拆散，便于操作
				for(let item in data){
					Vue.set(this,item,data[item])
				}
			})
		}
	}
</script>

<style scoped lang='scss'>
	#content{
		position: absolute;
		width: 100%;
	}
	.panel{
		margin:5px;
		.top{
			padding: 10px;
			background-color: #fff;
			.create_at{
				float: left;
				font-size: 0.2rem;
			}
			.tab{
				background-color: #e1e1e1;
				padding: 0 .1rem;
				border-radius:10%;
				float: right;
				font-size: 0.2rem;
			}
			.title{
				font-size:.35rem;
				text-align: left;
				font-weight: 600;
				line-height: .6rem;
			}
		}
		.content{
			.author{
				background-color: #fff;
				img{
					height:0.5rem;
					display: block;
					padding: .125rem;
					border-radius: 15%;
					float: left;
					cursor:pointer; 
				}
				span{
					float: left;
					line-height: .5rem;
					font-size: .3rem;
				}
			}
			.main-content{
				text-align: justify;
				font-size:.26rem;
				line-height: .4rem;
				padding: 10px;
				background-color: #fff;
			}
		}
		.replies_count{
			margin:2px 0;
			background-color: #fff;
			height:.5rem;
			line-height: .5rem;
			font-size: .26rem;
			text-align: justify;
			color: #2e2e2e;
			font-weight: 700;
			&:before{
				content:'';
				height:100%;
				float: left;
				width: 7px;
				background-color: #2e2e2e;
				display: block;
			}
		}
		.replies{
			.reply{
				margin-bottom: 2px;
			}
			.author{
				background-color: #fff;
				img{
					height:0.5rem;
					display: block;
					padding: .125rem;
					border-radius: 15%;
					float: left;
					cursor:pointer; 
				}
				.name{
					float: left;
					line-height: .5rem;
					font-size: .25rem;
				}
				.create_at{
					float:right;
					line-height: .5rem;
					font-size: .2rem;
					padding-right: 10px;
				}
			}

			.content{
				text-align: justify;
				font-size:.26rem;
				line-height: .4rem;
				padding: 10px;
				padding-top: 0;
				background-color: #fff;
				position: relative;

			}
		}
	}
	
</style>


